import { mount } from 'enzyme';
import React from 'react';
import { Button } from './Button';
import { defaultValues } from '../../constants/defaultValues';

export const Component = (
  isChecked,
  isDefaultChecked,
  isDisabled,
  isPartiallyChecked,
  position,
  children
) => {
  return (
    <Button
      isChecked={isChecked}
      isPartiallyChecked={isPartiallyChecked}
      isDefaultChecked={isDefaultChecked}
      isDisabled={isDisabled}
      position={position}
    >
      {children}
    </Button>
  );
};

let output;

export const tests = (
  button = Component(
    defaultValues.isChecked,
    defaultValues.isDefaultChecked,
    defaultValues.isDisabled,
    defaultValues.isPartiallyChecked,
    defaultValues.position,
    defaultValues.children
  )
) => {
  output = mount(button);

  return describe('Checkbox', () => {
    it(`Should have isDefaultChecked: ${button.props.isDefaultChecked}`, () => {
      expect(output.props().isDefaultChecked).toEqual(
        button.props.isDefaultChecked
      );
    });
  });
};

tests();
